.page-info {
  // background-color: #fda7df9f;
  .section1 {
    & .image-heading {
      width: 60%;
      margin: 0 auto;
    }
    & .quote-text {
      width: 47%;
      margin-left: auto;
    }

    &__quote--secondary {
      margin-top: 15rem;
    }
  }
  .section2 {
    margin-top: 15rem;
    display: flex;

    &__img {
      width: 70%;
      & img {
        filter: none;
      }
    }

    &__contacts {
      width: 30%;
      & p {
        font-size: 16px;
      }
      & div {
        margin-top: 1rem;
        font-size: 19px;
        color: #b20000;
      }
      & address {
        margin-top: 1rem;
        font-size: 19px;
        font-style: normal;
      }
    }
  }

  .section3 {
    margin-top: 5rem;
    display: flex;
    margin-left: 30%;

    &__head {
      text-align: left;
      font-size: 19px;
      font-style: normal;

      & address {
        font-style: normal;
      }
    }

    &__img1 {
      width: 100%;
      padding-right: 1rem;
    }

    &__img2 {
      width: 100%;
      padding-left: 1rem;
    }
  }
}

.section4 {
  margin: 10rem 0;
  display: flex;
  font-size: 19px;
  font-style: normal;

  &__heading {
    margin-left: 10rem;
    margin-right: 20rem;

    &-sub {
      margin-top: 4rem;
    }
  }

  &__action {
    & > * {
      display: flex;
      flex-direction: column;
    }
    &-sub {
      margin-top: 1rem;
    }
    &-job {
      & a {
        color: #b20000;
      }
    }
  }
}
.sub-input {
  border: none;
}

.submit-button {
  border: 1px solid #b20000;
  background-color: transparent;
  width: 50%;
}
